:root {
  --first-text-color: #fcfcfc;
  --second-text-color: #131313;
  --akcent-color: #f8d24e;
  --hover-color: #4d5ae5;
  --main-background-color: #84a178;
  --animatin-time: 250ms;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--main-background-color);
  max-width: 1600px;
  margin: 0 auto;
  font-family: Montserrat, sans-serif;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  outline: 1px solid #00f;
  margin: 0 auto;
  padding-top: 12px;
  position: relative;
}

.header .container {
  display: flex;
}

.logo-icon {
  width: 40px;
  height: 37px;
}

.header-menu {
  transition: transform var(--animatin-time) linear;
}

.header-menu.is-open {
  outline: 1px solid red;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 88px;
  display: flex;
  transform: translateX(100%);
}

.header-menu-icon {
  width: 32px;
  height: 32px;
}

.navigation {
  margin-bottom: 114px;
}

.header-menu-button {
  background-color: var(--main-background-color);
  border: none;
}

.icon-cross {
  display: none;
}

.header-menu-button.is-open .icon-cross {
  display: block;
}

.header-menu-button.is-open .icon-burger {
  display: none;
}

.navigation-list > li:not(:last-child) {
  margin-bottom: 32px;
}

.navigation-list > li {
  text-align: center;
}

.navigation-link {
  letter-spacing: 0;
  color: var(--first-text-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.22;
}

.navigation-link:hover {
  color: var(--second-text-color);
  cursor: url("cursor.40c92a4d.svg"), auto;
}

.navigation-button {
  background-color: var(--akcent-color);
  letter-spacing: 0;
  text-align: center;
  border: none;
  border-radius: 10px;
  padding: 12px 36px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.navigation-button:after {
  content: "";
  width: 12px;
  height: 12px;
  background-image: url("arrow.7a88aef5.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
  border-color: rgba(0, 0, 0, 0);
  margin-left: 4px;
  display: inline-block;
}

@media screen and (max-width: 1199px) {
  .header-menu {
    width: 100%;
    position: absolute;
    top: 100%;
    left: -100%;
  }

  .logo {
    margin-right: auto;
  }
}

@media screen and (min-width: 480px) {
  .header {
    max-width: 480px;
    padding-top: 20px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    max-width: 768px;
  }

  .navigation-button {
    white-space: nowrap;
    position: absolute;
    top: -43px;
    left: 180.2%;
  }

  .header-menu-button {
    margin-right: 164px;
  }

  .header-menu.is-open .navigation-button {
    display: none;
  }

  .navigation {
    margin-bottom: 0;
  }

  .header-menu.is-open {
    padding-bottom: 528px;
  }

  .header-menu-button.is-open {
    margin-right: 0;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    max-width: 1200px;
  }

  .header .container {
    justify-content: space-between;
    align-items: center;
  }

  .logo {
    margin-right: 0;
  }

  .header-menu {
    align-items: center;
    gap: 204px;
    display: flex;
    position: static;
  }

  .header-menu-button {
    display: none;
  }

  .navigation-button {
    position: static;
  }

  .navigation-list {
    flex-direction: row;
    display: flex;
  }

  .navigation-list > li:not(:last-child) {
    margin-bottom: 0;
    margin-right: 28px;
  }

  .navigation-link {
    letter-spacing: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.25;
  }

  .navigation-link:hover {
    color: var(--akcent-color);
  }

  .header-menu.is-open {
    width: auto;
    flex-direction: row;
    align-items: center;
    gap: 204px;
    padding: 0;
    display: flex;
    position: static;
    transform: translateX(0);
  }

  .header-menu.is-open .navigation-button {
    display: block;
    position: static;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  display: flex;
}

.product-card-header {
  text-align: center;
  margin-bottom: 83px;
}

.product-card {
  background-color: var(--first-text-color);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  margin: 0 auto;
  padding: 28px 74px;
}

.product-image-background {
  width: 132px;
  height: 132px;
  background-color: rgba(156, 190, 143, .7);
  border-radius: 50%;
  position: absolute;
  top: calc(7px - 48.5%);
  left: -2px;
  box-shadow: 0 5.26027px 19.726px rgba(10, 136, 23, .25);
}

.product-label {
  color: #131313;
  letter-spacing: .64px;
  text-transform: uppercase;
  border: 2px solid #96af8c;
  border-radius: 15px;
  padding: 96px 14px 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.22;
  display: block;
  position: relative;
  box-shadow: 0 5.26027px 19.726px rgba(212, 20, 67, .1);
}

.product-image-background .thumb {
  border-radius: 50%;
  position: absolute;
  top: -7px;
  overflow: hidden;
}

.product-input:checked + .product-label {
  background-color: #efd478;
  border-color: #efd478;
}

.product-input:checked + .product-label > .product-image-background {
  background-color: rgba(248, 210, 78, .7);
  box-shadow: 0 5.26027px 19.726px rgba(153, 114, 23, .25);
}

